"use client";

import React, { useState } from 'react';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { AlertDialog, AlertDialogAction, AlertDialogContent, AlertDialogDescription, AlertDialogHeader, AlertDialogTitle } from '@/components/ui/alert-dialog';
import { Progress } from '@/components/ui/progress';
import { toast } from 'sonner';

const prizes = [
  { name: '🚗 Brand New Car', rarity: 'legendary', color: 'from-yellow-400 to-orange-500' },
  { name: '📺 Smart TV 65"', rarity: 'epic', color: 'from-purple-400 to-pink-500' },
  { name: '✈️ Dream Vacation', rarity: 'epic', color: 'from-blue-400 to-cyan-500' },
  { name: '🎁 $500 Gift Card', rarity: 'rare', color: 'from-green-400 to-emerald-500' },
  { name: '🎮 Gaming Console', rarity: 'rare', color: 'from-indigo-400 to-purple-500' },
  { name: '💎 Jewelry Set', rarity: 'rare', color: 'from-pink-400 to-rose-500' },
  { name: '🎧 Premium Headphones', rarity: 'common', color: 'from-gray-400 to-slate-500' },
  { name: '☕ Coffee Voucher', rarity: 'common', color: 'from-amber-400 to-yellow-500' },
  { name: '🍀 Better Luck Next Time', rarity: 'common', color: 'from-red-400 to-pink-500' },
];

interface DrawEntry {
  prize: string;
  date: string;
  rarity: string;
}

export default function LuckyDrawGame() {
  const [isSpinning, setIsSpinning] = useState(false);
  const [result, setResult] = useState<typeof prizes[0] | null>(null);
  const [showResult, setShowResult] = useState(false);
  const [progress, setProgress] = useState(0);
  const [spinCount, setSpinCount] = useState(0);

  const handleSpin = async () => {
    if (isSpinning) return;
    
    setIsSpinning(true);
    setResult(null);
    setProgress(0);
    
    try {
      // Animate progress bar
      const progressInterval = setInterval(() => {
        setProgress(prev => {
          if (prev >= 100) {
            clearInterval(progressInterval);
            return 100;
          }
          return prev + 2;
        });
      }, 50);

      // Simulate spinning delay (2.5s)
      await new Promise((resolve) => setTimeout(resolve, 2500));
      
      // Random prize selection with weighted probability
      const randomValue = Math.random();
      let selectedPrize;
      
      if (randomValue < 0.01) { // 1% chance for legendary
        selectedPrize = prizes.find(p => p.rarity === 'legendary') || prizes[0];
      } else if (randomValue < 0.1) { // 9% chance for epic
        const epicPrizes = prizes.filter(p => p.rarity === 'epic');
        selectedPrize = epicPrizes[Math.floor(Math.random() * epicPrizes.length)];
      } else if (randomValue < 0.3) { // 20% chance for rare
        const rarePrizes = prizes.filter(p => p.rarity === 'rare');
        selectedPrize = rarePrizes[Math.floor(Math.random() * rarePrizes.length)];
      } else { // 70% chance for common
        const commonPrizes = prizes.filter(p => p.rarity === 'common');
        selectedPrize = commonPrizes[Math.floor(Math.random() * commonPrizes.length)];
      }
      
      setResult(selectedPrize);
      setSpinCount(prev => prev + 1);
      
      // Show appropriate toast based on rarity
      if (selectedPrize.rarity === 'legendary') {
        toast.success(`🎉 LEGENDARY WIN! ${selectedPrize.name}`, {
          duration: 5000,
        });
      } else if (selectedPrize.rarity === 'epic') {
        toast.success(`✨ EPIC WIN! ${selectedPrize.name}`, {
          duration: 4000,
        });
      } else if (selectedPrize.rarity === 'rare') {
        toast.success(`🌟 RARE WIN! ${selectedPrize.name}`, {
          duration: 3000,
        });
      } else {
        if (selectedPrize.name.includes('Better Luck')) {
          toast.info(`${selectedPrize.name} 🍀`, {
            duration: 2000,
          });
        } else {
          toast.success(`🎁 You won: ${selectedPrize.name}`, {
            duration: 2000,
          });
        }
      }
      
      setShowResult(true);
      
      // Save to history
      if (typeof window !== 'undefined') {
        const storedHistory = JSON.parse(localStorage.getItem('luckyDrawHistory') || '[]');
        const newEntry: DrawEntry = {
          prize: selectedPrize.name,
          date: new Date().toISOString(),
          rarity: selectedPrize.rarity
        };
        storedHistory.unshift(newEntry); // Add to beginning
        // Keep only last 50 entries
        if (storedHistory.length > 50) {
          storedHistory.splice(50);
        }
        localStorage.setItem('luckyDrawHistory', JSON.stringify(storedHistory));
        
        // Trigger storage event for history component
        window.dispatchEvent(new Event('storage'));
      }
    } catch (error) {
      toast.error('An error occurred while spinning. Please try again.');
      console.error('Spin error:', error);
    } finally {
      setIsSpinning(false);
      setProgress(0);
    }
  };

  const getRarityBadge = (rarity: string) => {
    const badges = {
      legendary: '👑 LEGENDARY',
      epic: '💜 EPIC',
      rare: '💙 RARE',
      common: '🤍 COMMON'
    };
    return badges[rarity as keyof typeof badges] || '🤍 COMMON';
  };

  return (
    <Card className="bg-white/10 backdrop-blur-md border-white/20 shadow-2xl">
      <CardHeader className="text-center">
        <CardTitle className="text-3xl font-bold text-white mb-2">
          🎰 Lucky Draw Wheel
        </CardTitle>
        <CardDescription className="text-white/80 text-lg">
          Press the spin button and test your luck!
        </CardDescription>
        <div className="text-white/60 text-sm">
          Total Spins: {spinCount}
        </div>
      </CardHeader>
      
      <CardContent className="space-y-6">
        {/* Prize Display Grid */}
        <div className="grid grid-cols-2 md:grid-cols-3 gap-3 mb-6">
          {prizes.slice(0, 6).map((prize, index) => (
            <div
              key={index}
              className={`p-3 rounded-lg bg-gradient-to-r ${prize.color} text-white text-center text-sm font-medium shadow-lg transform hover:scale-105 transition-transform`}
            >
              <div className="truncate">{prize.name}</div>
              <div className="text-xs opacity-80 mt-1">
                {getRarityBadge(prize.rarity)}
              </div>
            </div>
          ))}
        </div>

        {/* Progress Bar */}
        {isSpinning && (
          <div className="space-y-2">
            <div className="text-white text-center font-medium">
              🎲 Spinning the wheel...
            </div>
            <Progress value={progress} className="h-3" />
          </div>
        )}

        {/* Spin Button */}
        <Button 
          onClick={handleSpin} 
          disabled={isSpinning} 
          className="w-full h-16 text-xl font-bold bg-gradient-to-r from-pink-500 to-purple-600 hover:from-pink-600 hover:to-purple-700 transform hover:scale-105 transition-all duration-200 shadow-lg"
        >
          {isSpinning ? (
            <div className="flex items-center gap-2">
              <div className="animate-spin rounded-full h-6 w-6 border-b-2 border-white"></div>
              Spinning...
            </div>
          ) : (
            '🎰 SPIN THE WHEEL!'
          )}
        </Button>

        {/* Game Instructions */}
        <div className="text-white/70 text-sm text-center space-y-1">
          <p>🎯 Different prizes have different win rates</p>
          <p>👑 Legendary: 1% | 💜 Epic: 9% | 💙 Rare: 20% | 🤍 Common: 70%</p>
        </div>
      </CardContent>

      {/* Result Dialog */}
      <AlertDialog open={showResult} onOpenChange={setShowResult}>
        <AlertDialogContent className="bg-gradient-to-br from-purple-900 to-indigo-900 border-white/20">
          <AlertDialogHeader>
            <AlertDialogTitle className="text-2xl text-center text-white">
              🎉 Spin Result!
            </AlertDialogTitle>
            <AlertDialogDescription className="text-center">
              {result && (
                <div className="space-y-4">
                  <div className={`p-6 rounded-lg bg-gradient-to-r ${result.color} text-white text-center shadow-lg`}>
                    <div className="text-2xl font-bold mb-2">{result.name}</div>
                    <div className="text-lg opacity-90">
                      {getRarityBadge(result.rarity)}
                    </div>
                  </div>
                  <div className="text-white/80">
                    {result.name.includes('Better Luck') 
                      ? "Don't give up! Try spinning again!" 
                      : "Congratulations on your amazing win!"
                    }
                  </div>
                </div>
              )}
            </AlertDialogDescription>
          </AlertDialogHeader>
          <AlertDialogAction 
            onClick={() => setShowResult(false)}
            className="bg-gradient-to-r from-green-500 to-emerald-600 hover:from-green-600 hover:to-emerald-700"
          >
            Awesome! 🎊
          </AlertDialogAction>
        </AlertDialogContent>
      </AlertDialog>
    </Card>
  );
}
